.tab-wrapper {
  position: sticky;
  margin-bottom: 20px;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.tab-education{
  display: flex;
  flex-direction: row;
}

.tab-image{
  width:20vw;
  flex-grow:0;
  background:gray;
  border-radius: 10px;
  visibility: hidden;
}

.tab-text{
  width:60vw;
  display: flex;
  flex-direction: column;
  align-items: left;
}

.tab-header{
  text-align: center;
  text-indent: 0;
}

.tab-body{
  text-align: left;
  text-indent: 50px;
}
